import Activity from "./activity-model.js";
const activity = new Activity()
export default {
	data() {
		return {
			userInfo: {}
		};
	},
	onLoad() {
		// const that = this
		// that._onLoad()
	},
	methods: {
		_onLoad(callBack) {
			const that = this
			that.userInfo = that.$store.state.userInfo;
			that.getListData(() => {
				callBack && callBack();
			})
		},
		// 获取列表
		getListData(callBack) {
			const that = this
			activity.getListData({}, (res) => {
				if (res.status_code == 'ok') {
					let userInfo = that.$store.state.userInfo;
					that.userInfo = Object.assign(userInfo, res.data)
					that.$store.commit('updateUserInfo', that.userInfo);
				}
				callBack && callBack();
			})
		},
		
		goSignIn(e) {
			// const that = this;
			// const id = activity.get_data_set(e, "id");
			activity.navigate_to(`/pages/signIn/signIn`);
		},
		goShere(e) {
			// const that = this;
			// const id = activity.get_data_set(e, "id");
			activity.navigate_to(`/pages/shere/shere`);
		},
	},
	// 下拉刷新
	onPullDownRefresh() {
	  var that = this;
	  that.page = 1;
	  that._onLoad(() => {
	    uni.stopPullDownRefresh();
	  });
	},
	//上拉加载更多
	// onReachBottom() {
	//   var that = this;
	//   if (that.last_page == that.page) {
	//     return;
	//   }
	//   that.page += 1;
	//   that.getListData();
	// },
	// 分享
	onShareAppMessage() {
		let shareData = {
			title: '',
			path: `pages/index/index?${this.userInfo.id}`,
			imageUrl: ''
		}
		return activity.onShareAppMessage(shareData);
	}
}
